@-webkit-keyframes unblur {
    0% {-webkit-filter: blur(10px);filter: blur(10px);}
    20% {-webkit-filter: blur(11px);filter: blur(11px);}
    40% {-webkit-filter: blur(8px);filter: blur(8px);}
    60% {-webkit-filter: blur(12px);filter: blur(12px);}
    100% {-webkit-filter: blur(0px);filter: blur(0px);}
  }

@keyframes unblur {
    0% {-webkit-filter: blur(10px);filter: blur(10px);}
    20% {-webkit-filter: blur(11px);filter: blur(11px);}
    40% {-webkit-filter: blur(8px);filter: blur(8px);}
    60% {-webkit-filter: blur(12px);filter: blur(12px);}
    100% {-webkit-filter: blur(0px);filter: blur(0px);}
  }

@-webkit-keyframes Minimize {
