.gallery-container{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  overflow : visible
}

.gallery-container .heading{
  background-image: url(/static/media/971A0840.3ce786a3.jpg);
  background-size: cover;
  background-position-y: 25%;
  min-width: 100%;
  height : 4%;
  margin-bottom: 7%;
}

.GalleryHeadline{
  justify-content: center;
  font-size: 48px;
  height : 100%;
  color: whitesmoke;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0,0,0,0.3);

}





.gallery-container .heading h3 span{
  font-weight: 100;
}

.gallery-container .box2{
 display: flex;
 flex-direction: row;
 justify-content: space-between;
 margin: 40px 20px 0 20px;
}

.gallery-container .box2 .dream{
  display: flex;
  flex-direction: column;
  width: 24.5%;
  overflow: hidden;
}

.gallery-container .box2 .dream img{
  width: 100%;
  padding-bottom: 15px;
  border-radius: 5px;
  overflow: hidden;
  margin : auto;
}



.gallery-container .btn{
  margin: 40px 0 70px 0;
  background: #222;
  padding: 15px 40px ;
  border-radius: 5px;
}

.gallery-container .btn a{
 color: #fff;
 font-size: 1.2em;
 text-decoration: none;
 font-weight: bolder;
 letter-spacing: 3px;
}

@media only screen and (max-width: 769px){
    .gallery-container .box2{
   flex-direction: column;
  }

.gallery-container .box2 .dream{
  width: 100%;
}





}

@media only screen and (max-width: 643px){
.gallery-container .heading{
  width: 100%;
}
.gallery-container .heading h3{
  font-size: 1em;

}







}

.container{
    display: flex;
    align-items : center;
    text-align: center;
    margin : 0;
    width: 200%;
    height : 100vh;
    background-color: #141414;
}

.slide{
    margin-left: 0;
    width : 50%;
    display: flex;
    flex-direction: row;
    align-items : center;
    text-align: center;
    transition-duration: 3s;
}

#radio1:checked ~ .first{
    margin-left: 0%;
}

#radio2:checked ~ .first{
    margin-left: -50%;
}

.container .img-container {
    width : 24.850%;
    height : 100vh;
    display: flex;
    margin-right: 0.15%;
}

.container .img-container img{
    width : 100%;
    height : 100%; 
    transition: -webkit-transform .5s ease-in-out; 
    transition: transform .5s ease-in-out; 
    transition: transform .5s ease-in-out, -webkit-transform .5s ease-in-out; /* Animation */
    overflow : hidden;
  
}

.container .left-button{
    margin-left: 20px;
    position: absolute;
    z-index: 10;
    background-color: transparent;
}

.container .right-button{
    margin-left: 20px;
    position: absolute;
    z-index: 10;
    background-color: transparent;
    right: 1%;
}


.container .img-container .text-overlay{
    width : 25.0%;
    height: 20%;
    background-image: linear-gradient(to bottom,rgba(0,0,0,0), rgba(0,0,0,0.9));
    color : white;
    font-size: 40px;
    border-radius: 0px;
    position: absolute;
    z-index: 10;
    align-self: flex-end;   
}


.container .img-container .description-overlay:hover{ 
    z-index: 11;
    opacity: 1; 
}  

.container .img-container .description-overlay:hover ~ #img{ 
    -webkit-transform: scale(1.2) ; 
            transform: scale(1.2) ;
    overflow : hidden;

}  

.container .headline-overlay{
    width : 100%;
    height: 10%;
    background-image: linear-gradient(to bottom, rgba(0,0,0,0.6), rgba(0,0,0,0));
    color : white;
    font-size: 40px;
    border-radius: 0px;
    position: absolute;
    z-index: 10;
    align-self: flex-start;   
    justify-self: flex-start;
}

.container .headline-overlay span{
    position: relative;
    top : 40%;
    right: center;
}

.container .img-container .text-overlay span{
    position: relative;
    top : 30%;
    right: 0%;
}


.container .img-container img:hover{
    -webkit-transform: scale(1.2) ;
            transform: scale(1.2) ;
    overflow : hidden;
}


